"use client";
import React, { useState } from "react";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import Category from "./category";
import Order from "./order";

interface MenuItem {
  name: string;
  description: string;
  price: number;
  imageUrl: string;
}

const ingredientsList = ["Cheese", "Tomatoes", "Lettuce", "Onions", "Bacon", "Mushrooms"];

const MenuCard: React.FC<MenuItem & { onClick: (item: MenuItem) => void }> = ({ name, description, price, imageUrl, onClick }) => {
  const cardStyle: React.CSSProperties = {
    borderRadius: "12px",
    backgroundColor: "#ffffff",
    boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
    overflow: "hidden",
    transition: "transform 0.2s, box-shadow 0.2s",
    cursor: "pointer",
  };

  const imageStyle: React.CSSProperties = {
    width: "100%",
    height: "160px",
    objectFit: "cover",
  };

  const contentStyle: React.CSSProperties = {
    padding: "16px",
  };

  const titleStyle: React.CSSProperties = {
    fontSize: "18px",
    fontWeight: "bold",
    marginBottom: "8px",
  };

  const descriptionStyle: React.CSSProperties = {
    fontSize: "14px",
    color: "#666666",
    marginBottom: "12px",
  };

  const priceStyle: React.CSSProperties = {
    fontSize: "16px",
    fontWeight: "bold",
    color: "#FFB800",
  };

  return (
    <div
      style={cardStyle}
      onMouseEnter={(e) => {
        e.currentTarget.style.transform = "translateY(-4px)";
        e.currentTarget.style.boxShadow = "0 6px 12px rgba(0, 0, 0, 0.15)";
      }}
      onMouseLeave={(e) => {
        e.currentTarget.style.transform = "none";
        e.currentTarget.style.boxShadow = "0 4px 6px rgba(0, 0, 0, 0.1)";
      }}
      onClick={() => onClick({ name, description, price, imageUrl })}
    >
      <img src={imageUrl} alt={name} style={imageStyle} />
      <div style={contentStyle}>
        <h3 style={titleStyle}>{name}</h3>
        <p style={descriptionStyle}>{description}</p>
        <p style={priceStyle}>${price.toFixed(2)}</p>
      </div>
    </div>
  );
};

const Restaurant: React.FC = () => {
  const [selectedItem, setSelectedItem] = useState<MenuItem | null>(null);
  const [selectedIngredients, setSelectedIngredients] = useState<string[]>([]);

  const handleIngredientChange = (ingredient: string) => {
    setSelectedIngredients((prev) =>
      prev.includes(ingredient) ? prev.filter((item) => item !== ingredient) : [...prev, ingredient]
    );
  };

  const containerStyle: React.CSSProperties = {
    display: "flex",
    flexDirection: "column",
    gap: "24px",
    padding: "24px",
  };

  const mainContentStyle: React.CSSProperties = {
    display: "flex",
    gap: "24px",
  };

  const menuGridStyle: React.CSSProperties = {
    display: "grid",
    gridTemplateColumns: "repeat(auto-fill, minmax(250px, 1fr))",
    gap: "24px",
  };

  const orderPanelStyle: React.CSSProperties = {
    width: "300px",
    backgroundColor: "#ffffff",
    borderRadius: "12px",
    padding: "16px",
    boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
  };

  const modalOverlayStyle: React.CSSProperties = {
    position: "fixed",
    top: 0,
    left: 0,
    width: "100%",
    height: "100%",
    backgroundColor: "rgba(0, 0, 0, 0.5)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  };

  const modalContentStyle: React.CSSProperties = {
    backgroundColor: "white",
    padding: "20px",
    borderRadius: "12px",
    width: "400px",
  };

  const modalButtonStyle: React.CSSProperties = {
    padding: "8px 12px",
    cursor: "pointer",
  };

  const modalAddButtonStyle: React.CSSProperties = {
    ...modalButtonStyle,
    backgroundColor: "#FFB800",
    color: "white",
  };

  return (
    <div style={containerStyle}>
      <Navbar />
      <div style={mainContentStyle}>
        <Sidebar />
        <div style={{ flex: 1 }}>
          <Category />
          <div style={menuGridStyle}>
            {[
              {
                name: "Cheeseburger",
                description: "Juicy beef patty with melted cheese, lettuce, and tomato.",
                price: 9.99,
                imageUrl: "https://via.placeholder.com/250x160",
              },
              {
                name: "Margherita Pizza",
                description: "Classic pizza with fresh mozzarella, tomatoes, and basil.",
                price: 12.99,
                imageUrl: "https://via.placeholder.com/250x160",
              },
            ].map((item, index) => (
              <MenuCard key={index} {...item} onClick={setSelectedItem} />
            ))}
          </div>
        </div>
        <div style={orderPanelStyle}>
          <Order />
        </div>
      </div>
      {selectedItem && (
        <div style={modalOverlayStyle}>
          <div style={modalContentStyle}>
            <h2>{selectedItem.name}</h2>
            <img src={selectedItem.imageUrl} alt={selectedItem.name} style={{ width: "100%", borderRadius: "8px" }} />
            <p>{selectedItem.description}</p>
            <p style={{ fontSize: "18px", fontWeight: "bold" }}>${selectedItem.price.toFixed(2)}</p>
            <h3>Choose Ingredients:</h3>
            <div>
              {ingredientsList.map((ingredient) => (
                <label key={ingredient} style={{ display: "block", marginBottom: "8px" }}>
                  <input
                    type="checkbox"
                    checked={selectedIngredients.includes(ingredient)}
                    onChange={() => handleIngredientChange(ingredient)}
                  />
                  {ingredient}
                </label>
              ))}
            </div>
            <div style={{ marginTop: "16px", display: "flex", justifyContent: "space-between" }}>
              <button onClick={() => setSelectedItem(null)} style={modalButtonStyle}>
                Close
              </button>
              <button style={modalAddButtonStyle}>Add to Order</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Restaurant;